@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;700;900&display=swap');

*{
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
}

body{
    font-family: 'Rubik', sans-serif;
    background: #0D0D2B;
    color: #FFFFFF;
}

.Container{
    max-width: 1202px;
    margin: auto;
}



/* HEADER -- START */

.Header{
    padding: 60px 0 110px;
}
.Navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .NavList{
        display: flex;
        .NavLink{
            margin-right: 35px;
            color: #FFFFFF;
        }
        .NavBtn{
            padding: 14px 32px;
            background: #3671E9;
            border-radius: 32px;
            font-weight: 500;
            color: #FFFFFF;
        }
    }
}
.HeaderInfo{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 100px;
    .Sale{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 6px 11px 6px 6px;
        background: #252540;
        max-width: 365px;
        border-radius: 32px;
        margin-bottom: 25px;
        .saleItem{
            padding: 6px 16px;
            background: #FFFFFF;
            border-radius: 32px;
            font-weight: 500;
            color: #0D0D2B;
        }
    }
    .Title{
        font-weight: 700;
        font-size: 64px;
        margin-bottom: 25px;
    }
    .Subtitle{
        letter-spacing: 0.01em;
        color: #E0E0E0;
        margin-bottom: 42px;
    }
    .HeaderBtn{
        padding: 18px 24px;
        background: #3671E9;
        border-radius: 32px;
        font-weight: 500;
        font-size: 18px;
        color: #FFFFFF;
    }
}

/* HEADER -- END */


/* SECTION -- COUNTER -- START */

.CounterBox{
    padding: 120px 0;
    display: flex;
    justify-content: space-between;
    .CounterItem{
        display: flex;
        align-items: center;
        .CounterText{
            margin-left: 25px;
            .CounterTitle{
                font-size: 40px;
                margin-bottom: 10px;
            }
            .CounterSubtitle{
                color: #E0E0E0;
            }
        }
    }
}

/* SECTION -- COUNTER -- END */


/* SECTION -- CALCULATOR -- START */

.SectionCalculator{
    padding-top: 100px;
    .CalculatorText{
        text-align: center;
        margin-bottom: 65px;
        .CalculatorTitle{
            font-size: 40px;
            margin-bottom: 30px;
        }
        .CalculatorSubtitle{
            font-size: 18px;
            color: #E0E0E0;
        }
    }
    .CalculatorBox{
        max-width: 942px;
        padding: 50px;
        margin: auto;
        background: #f6f8fd;
        border-radius: 16px;
        color: #0D0D2B;
        margin-bottom: -155px;
        box-shadow: 0px 80px 40px #391777;
        border: 1px solid #000000;
        .CalculatorForm{
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 80px;
            .CryptoName{
                width: 400px;
                border: none;
                border-bottom: 2px solid #E0E0E0;
                font-size: 20px;
                padding-bottom: 10px;
                &:focus{
                    outline: none;
                }
            }
            .CryptoNum{
                width: 200px;
                border: none;
                border-bottom: 2px solid #E0E0E0;
                font-size: 20px;
                padding-bottom: 10px;
                &:focus{
                    outline: none;
                }
            }
            .CalculatorBtn{
                padding: 18px 32px;
                background: #3671E9;
                border-radius: 32px;
                border: none;
                cursor: pointer;
                color: #FFFFFF;
                font-weight: 500;
                font-size: 18px;
            }
        }
        .Calculator24{
            font-weight: 500;
            color: #3671E9;
            letter-spacing: 0.04em;
            margin-bottom: 20px;
        }
        .CalculatorPrice{
            font-size: 32px;
            letter-spacing: 0.04em;
            color: #0D0D2B;
            margin-bottom: 5px;
        }
        .CalculatorBoxSubtile{
            letter-spacing: 0.01em;
            color: #828282;
        }
    }
}

/* SECTION -- CALCULATOR -- END */


/* SECTION -- CRYPTO -- START */

.SectionCrypto{
    padding: 300px 0 100px;
    background: linear-gradient(180deg, #F8F9FB 0%, #FAFBFF 100%);
    .CryptoText{
        text-align: center;
        margin-bottom: 65px;
        .CrytoTitle{
            color: #0D0D2B;
            font-size: 40px;
        }  
    }
    .CryptoBox{
        display: flex;
        justify-content: space-between;
        .CryptoItem{
            padding: 48px 35px;
            max-width: 370px;
            background: #2B076E;
            text-align: center;
            border-radius: 16px;
            .CryptoItemText{
                margin: 35px 0 25px;
                display: flex;
                justify-content: center;
                align-items: center;
                .CryptoItemTitle{
                    font-size: 32px;
                    margin-right: 10px;
                }
                span{
                    font-weight: 500;
                    font-size: 18px;
                    color: #FFFFFF;
                    opacity: 0.7;
                }
            }
            .CryptoItemSubtitle{
                letter-spacing: 0.01em;
                line-height: 28px;
                margin-bottom: 30px;
            }
            .CryptoBtn{
                padding: 20px 24px;
                border-radius: 50%;
                border: none;
                cursor: pointer;
            }
        }
    }
}

/* SECTION -- CRYPTO -- END */


/* SECTION -- CONTACT -- START */

.SectionContact{
    padding: 88px 0;
    background: linear-gradient(180deg, #2B076E 0%, #0D0D2B 100%);
    .ContactBox{
        padding: 50px;
        background: #3671E9;
        border-radius: 16px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .ContactBoxText{
            .ContactTitle{
                font-size: 32px;
                margin-bottom: 20px;
            }
            .ContactSubtitle{
                line-height: 28px;
                letter-spacing: 0.01em;
            }
        }
        input{
            width: 360px;
            border: none;
            border-bottom: 2px solid rgba(255, 255, 255, 0.4);
            background: transparent;
            color: #ffffff;
            padding-bottom: 10px;
            font-size: 16px;
            margin-right: 40px;
            &:focus{
                outline: none;
            }
            &::placeholder{
                color: #FFFFFF;
            }
        }
        button{
            padding: 15px 32px;
            background: #FFFFFF;
            border-radius: 32px;
            border: none;
            cursor: pointer;
            font-weight: 500;
            font-size: 18px;
            color: #0D0D2B;
        }    
    }
}

/* SECTION -- CONTACT -- END */


.Footer{
    padding: 75px 0;
    background: #0D0D2B;
    .FooterInfo{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .FooterTitle{
            letter-spacing: 0.01em;
        }
    }
    .Icons{
        margin-right: 40px;
        color: #FFFFFF;
    }
}